.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.y-grid .tick line{
  stroke: #4b5f87;
  opacity: 1;
}

.histogram {
  .bar {
    rect {
      fill: steelblue;
      shape-rendering: crispEdges;
    }
    text {
      fill: #fff;
      font: 12px sans-serif;
    }
  }
}

body {
  background-color: #2c313b;
  font-family: 'Roboto', sans-serif !important;
  padding-top:10px;
}

h3{
    color: #f8fdff;
    padding-left: 20px;
    padding-top: 20px;
    opacity: .8;
    font-weight: 400;
    font-size: 20px;
}

.top{
    margin-bottom: 10px;
    background-color: #3f5175;
    border-radius:4px;
    height: 400px;
    -webkit-box-shadow: 1px 1px 1px 0px rgba(37,41,51,1);
            box-shadow: 1px 1px 1px 0px rgba(37,41,51,1);
}

.bottom-left{
    background-color: #3f5175;
    border-radius: 4px;
    height: 400px;
    -webkit-box-shadow: 1px 1px 1px 0px rgba(37,41,51,1);
            box-shadow: 1px 1px 1px 0px rgba(37,41,51,1);
}

.bottom-right{
    background-color: #3f5175;
    border-radius: 4px;
    height: 400px;

    -webkit-box-shadow: 1px 1px 1px 0px rgba(37,41,51,1);

            box-shadow: 1px 1px 1px 0px rgba(37,41,51,1);
}

.shadow {
    -webkit-filter: drop-shadow( 0px 2px 2px rgba(0,0,0,.3) );
    filter: drop-shadow( 0px 2px 2px rgba(0,0,0,.3) );
}

.pad{
    padding:20px;
}

.browser-legend{
    font-size:14px;
    opacity:.8;
}

.bottom-left-svg{
    padding-top:40px;
    text-align: center;
}


.bottom-right-svg{
    text-align: center;
}

.line {
  fill: none;
  stroke: #7dc7f4;
  stroke-width: 5px;
}

.axis{
  fill:#bbc7d9;
  opacity: 1;
}

.axis path {
  fill: none;
  stroke: none;
  shape-rendering: crispEdges;
}

.axis line {
  fill: none;
  stroke: none;
  shape-rendering: crispEdges;
}

.axis text {
  font-size: 14px;
}



